
   
   * {
      box-sizing: border-box;
   }
   html, body, #root {
      height: 100%;
      width: 100%;
      top: 0;
      left: 0;
      margin: 0;
      padding: 0;
   }
   body {
      font-family: 'Autobahn', sans-serif;
      background: #341e5d;
      color: #101015;
   }
   #root {    display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;}
   
   #unity-desktop {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: all;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      z-index: 5;
   }
   #unity-loading-bar {
      display: none;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
   }
   #unity-logo {
      width: 154px;
      height: 130px;
      background: url('./TemplateData/unity-logo-dark.png') no-repeat center;
   }
   #progressBar {
      position: absolute;
      top: 33%;
      width: 100%;
      z-index: 1;
      padding: 3%;
      white-space: nowrap;
      text-align: center;
      font-family: 'DestructoBeam BB', sans-serif;
      font-size: 42px;
      /*color: #fffac8;*/
      background: linear-gradient(45deg, rgba(3, 107, 126, 0.77), rgba(252, 255, 169, 0.84));
      -webkit-background-clip: text;
      color: transparent;
   }
   #unity-progress-bar-empty {
      position: relative;
      width: 141px;
      height: 18px;
      margin-top: 10px;
      margin-left: 6.5px;
      background: url('./TemplateData/progress-bar-empty-dark.png') no-repeat center
   }
   #unity-progress-bar-full {
      position: relative;
      width: 0;
      height: 18px;
      margin-top: 10px;
      background: url('./TemplateData/progress-bar-full-dark.png') no-repeat center;
   }
   #unity-webgl-logo {
      float: left;
      width: 204px;
      height: 38px;
      background: url('./TemplateData/webgl-logo.png') no-repeat center;
   }
   #unity-build-title {
      float: right;
      margin: 0;
      margin-right: 10px;
      line-height: 50px;
      font-size: 30px;
      text-align: center;
      font-family: Origicide, Garamond, Tahoma, sans-serif;
      border-radius: 10px;
      filter: drop-shadow(0px 0px 2px black);
      background: linear-gradient(45deg, rgba(69, 0, 213, 0.88), rgba(134, 0, 166, 0.91));
      -webkit-background-clip: text;
      color: transparent;
   }
   
   #unity-wrapper {
      /*position: absolute;*/
      top: 0;
      left: 0;
      width: max-content;
      height: max-content;
      pointer-events: all;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
   }
   #unity-footer {
      position: relative;
      width: 100%;
      height: 50px;
      top: 0;
      background: linear-gradient(45deg, rgba(91, 9, 255, 0.88), rgba(252, 255, 169, 0.91));
      box-shadow: 0px 0px 23px 5px #1a0131 inset;
      z-index: 99;
   }
   #unity-container {
      width: 1366px;
      height: 768px;
      pointer-events: all;
   }

   #unity-buttons-container {
      position: relative;
      top: 0;
      display: flex;
      align-items: center;
      height: max-content;
      justify-content: space-between;
      width: 100%;
      padding: 5px;
      background: linear-gradient(45deg, rgba(2, 120, 171, 0.77), rgba(252, 255, 169, 0.84));
      box-shadow: 0px 0px 23px 5px #1a0131 inset;
   }
   #unity-fullscreen-button {
      width: auto;
      height: 40px;
      font-size: 30px;
      text-align: center;
      font-family: Origicide, Garamond, Tahoma, sans-serif;
      border-radius: 10px;
      filter: drop-shadow(0px 0px 2px black);
      background: linear-gradient(45deg, rgba(69, 0, 213, 0.88), rgba(134, 0, 166, 0.91));
      -webkit-background-clip: text;
      color: transparent;
      z-index: 9;
   }
   
   canvas#react-unity-webgl-canvas-2 {
      background: #231F20;
   }
   #unity-container.unity-mobile {
      width: 100%;
      height: 100%;
   }
   .unity-mobile canvas#react-unity-webgl-canvas-2 {
      width: 100%;
      height: 100%;
   }
   .unity-mobile #unity-footer {
      display: none;
   }
   
